"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { EditorToolbar } from "./components/EditorToolbar";
import { SectionSelector } from "./components/SectionSelector";
import EditorContent from "./components/EditorContent";
import { PreviewPanel } from "./components/PreviewPanel";
import { Editor } from "@tiptap/react";

interface TemplateContent {
    header: string;
    body: string;
    footer: string;
}

export const TemplateEditor = () => {
    const router = useRouter();
    const [loading, setLoading] = useState(false);
    const [currentSection, setCurrentSection] = useState<keyof TemplateContent>("body");
    const [editor, setEditor] = useState<Editor | null>(null);
    const [content, setContent] = useState<TemplateContent>({
        header: "",
        body: "",
        footer: "",
    });

    const handleCancel = () => {
        router.back();
    };

    const handleContentChange = (newContent: string) => {
        setContent((prev) => ({
            ...prev,
            [currentSection]: newContent,
        }));
    };

    const handleSave = async () => {
        setLoading(true);
        try {
            // Implementaremos esto en la siguiente fase
            console.log("Saving template:", content);
            await new Promise((resolve) => setTimeout(resolve, 1000));
            router.push("/dashboard");
        } catch (error) {
            console.error("Error saving template:", error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="p-6">
            <div className="flex justify-between items-center mb-6">
                <h1 className="text-2xl font-bold text-gray-900">Nueva Plantilla</h1>
                <div className="flex gap-4">
                    <button
                        onClick={handleCancel}
                        className="px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                    >
                        Cancelar
                    </button>
                    <button
                        onClick={handleSave}
                        disabled={loading}
                        className="px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50"
                    >
                        {loading ? "Guardando..." : "Guardar"}
                    </button>
                </div>
            </div>
            <div className="bg-white shadow rounded-lg flex">
                <div className="flex-1 flex flex-col">
                    <EditorToolbar editor={editor} />
                    <SectionSelector
                        currentSection={currentSection}
                        onSectionChange={(id) => setCurrentSection(id as keyof TemplateContent)}
                    />
                    <EditorContent
                        key={currentSection}
                        section={currentSection}
                        content={content[currentSection]}
                        onChange={handleContentChange}
                    />
                </div>
                <PreviewPanel content={content} />
            </div>
        </div>
    );
};
